import React from 'react'
import IconStartegy from '../icons/IconStartegy'

const Post = (props) => {
  return (
    <div className='content--block'>
      <div className='content--block--header'>
        {IconStartegy[props.postData.iconType]()}
        <div className='content--block--title'>
          {props.postData.title}
        </div>
      </div>
      {props.postData.subposts.map((it) => (
        <div className='content--block--part'>
          {IconStartegy['DOT']()}
          <div className='part-content'>
            <h4>{it.title}</h4>
            <p>{it.description}</p>
          </div>
        </div>
      ))}
    </div>
  )
};

export default Post


