import React, {Fragment} from 'react';
import {BarLoader} from 'react-spinners';
import Post from './Post'
import {getFeedData} from "../../service";
import {STATE} from '../../utils/'

class Feed extends React.Component {
  state = {
    dataStatus: STATE.NOT_TOUCHED,
    data: null
  };

  componentDidMount() {
    getFeedData()
      .then((res) => {
        this.setState({dataStatus: STATE.SUCCESS, data: res})
      })
      .catch((err)=>{
        this.setState({dataStatus: STATE.ERROR, data: err})
      });
  }

  render() {
    if (this.state.dataStatus === STATE.NOT_TOUCHED) {
      return (
        <div className='content'>
          <div className='spinner'>
            <BarLoader width={100} height={4}/>
          </div>
        </div>
      )
    }
    if (this.state.dataStatus === STATE.ERROR) {
      return (
        <div className='content'>
          Error :(
        </div>
      )
    }
    return (
      <Fragment>
        <div className='content'>
          {
            this.state.data.map((it)=>(
              <Post postData={it}/>
            ))
          }
        </div>
      </Fragment>
    )
  }
};

export default Feed


