import React from 'react'
import img from '../../../public/man.jpeg'
import {STATE} from '../../utils/'
import {getUserData} from '../../service/'
import {BarLoader} from "react-spinners";
import IconStartegy from '../../components/icons/IconStartegy'

class SideBar extends React.Component {

  state = {
    dataStatus: STATE.NOT_TOUCHED,
    data: null
  };

  componentDidMount() {
    getUserData()
      .then((res) => {
        this.setState({dataStatus: STATE.SUCCESS, data: res})
      })
  }

  render() {
    if (this.state.dataStatus === STATE.NOT_TOUCHED) {
      return (
        <div className='side-bar'>
          <div className='spinner'>
            <BarLoader width={100} height={4} color={'#fff'}/>
          </div>
        </div>
      )
    }
    if (this.state.dataStatus === STATE.ERROR) {
      return (
        <div className='side-bar'>
          Error :(
        </div>
      )
    }
    return (
      <div className='side-bar'>
        <div className='side-bar--logo' style={{backgroundImage: `url(${img})`}}>
          <div className='logo-block'>
            <p>Danil Sitdikov</p>
            <p>Front - end Developer</p>
          </div>
        </div>

        <div className='side-bar-content'>

          {this.state.data.map((it) => (
            <div className='side-bar--block'>
              {IconStartegy[it.iconType]()}
              <div className='side-bar--block--item'>
                <h4>{it.title}</h4>
                <p>
                  {it.description}
                </p>
              </div>
            </div>
          ))}
        </div>

      </div>
    )
  }
}

export default SideBar


